    justify-content: space-between;
    align-items: center;
    box-shadow: rgba(0, 0, 0, 0.02) 0px 1px 3px 0px, rgba(27, 31, 35, 0.15) 0px 0px 0px 1px;

    
   
    
}

.about-section .sec1{

   
    max-width: 40rem;
    flex-direction: column;
    display: flex;
    
    padding-top: 5rem;
    
    padding: 2rem 1rem;
    margin-top:  5rem; 
    margin-left: 7rem;
}

.about-section .sec2{

     margin-top: 3rem;
     display: flex;
     justify-content: center;
     align-items: center;
      border-radius: 1rem;
   margin-right: 3rem;
   margin-bottom: 3rem;
   overflow: hidden;
   box-shadow: rgb(85, 91, 255) 0px 0px 0px 3px, rgb(31, 193, 27) 0px 0px 0px 6px, rgb(255, 217, 19) 0px 0px 0px 9px, rgb(255, 156, 85) 0px 0px 0px 12px, rgb(255, 85, 85) 0px 0px 0px 15px;
   
 
}





.about-section .sec2 img{


     
    min-width: 10rem;
  
    min-height: 10rem;
  
  
    max-width: 20rem;
    max-height: 25rem;

 



}











.card {
    
    background:white;
    position: relative;
    display: flex;
    place-content: center;
    place-items: center;
    overflow: hidden;
    border-radius: 1.3rem;
    min-width: 10rem;
  
    min-height: 10rem;
  
  
    max-width: 25rem;
    max-height: 30rem;
  }
  
  .card img {
    z-index: 1;
    
  }
  
  .card::before {
    content: '';
    position: absolute;
    width: 100px;
    background-image: linear-gradient(180deg, rgb(0, 183, 255), rgb(255, 48, 255));
    height: 130%;
    animation: rotBGimg 3s linear infinite;
    transition: all 0.2s linear;
  }
  
  @keyframes rotBGimg {
    from {
      transform: rotate(0deg);
    }
  
    to {
      transform: rotate(360deg);
    }
  }
  
  .card::after {
    content: '';
    position: absolute;
    background: white;
    ;
    inset: 5px;
    border-radius: 15px;
  }  
  /* .card:hover:before {
    background-image: linear-gradient(180deg, rgb(81, 255, 0), purple);
    animation: rotBGimg 3.5s linear infinite;
  } */
  
  















