.footer-mobile{

    width: 100%;
    height: 70px;
    background-color: rgb(0, 0, 0);
    display: flex;
    
    align-items: center;
    margin-top: auto;
    position: fixed;
    bottom: 0;
    
}

.footer-mobile ul{

    display: flex;
    width: 100%;
    justify-content: space-evenly;
    align-items: center;
    
   
    
  
    
}

.footer-mobile ul li{

   position: relative;
    list-style: none;
    width: 70px;
    height: 55px;
    z-index: 1;
    

   
   
}


.footer-mobile ul li a{

    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 100%;
    text-align: center;
    font-weight: 500;



}


.footer-mobile ul li a .icon{


    position: relative;
    display: block;
    line-height: 50px;
    font-size: 1.5em;
    text-align: center;
    transition: 0.5s;
    color: white;
}


.footer-mobile ul li.active a .icon{


    transform: translateY(-32px);
}

.footer-mobile ul li a .text{

    position: absolute;
    color: white;
    font-weight: 400;
    font-size: 0.75em;
    letter-spacing: 0.05em;
    transition: 0.5s;
    opacity: 0;
    transform: translateY(20px);
}


.footer-mobile ul li.active a .text{


opacity: 1;
transform: translateY(10px);


}




.indicator{

    position: absolute;
    top: -50%;
    width: 70px;
    height: 55px;
    background-color: red;
    border-radius: 50%;
    border: 6px solid white;
    transition: 0.5s;


}



.indicator::before{

    content: '';
    position: absolute;
    top: 50%;
    left: -22px;
    width: 20px;
    height: 20px;
    background: transparent;
    border-top-right-radius: 20px;
    box-shadow: -1px -10px 0 0 white;
   
}



.indicator::after{

    content: '';
    position: absolute;
    top: 50%;
    right: -22px;
    width: 20px;
    height: 20px;
    background: transparent;
    border-top-left-radius: 20px;
    box-shadow: -1px -10px 0 0 white;
}




.footer-mobile ul li:nth-child(1).active ~ .footer-mobile{



    transform: translateX(calc(70px * 0));
    
}


.footer-mobile ul li:nth-child(2).active ~ .footer-mobile{



    transform: translateX(calc(70px * 1));
    
    }


    .footer-mobile ul li:nth-child(3).active ~ .footer-mobile{



        transform: translateX(calc(70px * 2));
        
        }



        .footer-mobile ul li:nth-child(4).active ~ .footer-mobile{



            transform: translateX(calc(70px * 3));
            
            }


          